 * {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
 }

 body {
   background: #131419;
 }

 h1 {
   padding-bottom: 20px;
   color: #64e5f9
 }

 h3 {
   color: #404040;
 }



.app {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10%;
  margin-top: 300px;
}

.container {
  text-align: center;
  padding: 2em;
  background: #131419;
  border-radius: 10px;
  box-shadow: -5px -5px 10px rgba(255,255,255,0.05),
               15px 15px 5px rgba(0,0,0,0.05);
}

.flex {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
}

.box {
padding: 0.5em;
min-width: 200px;
margin: 1em;
width: 100%;
font-size: 1em;
text-transform: uppercase;
border-radius: 10px;
padding: 5px 15px;
box-shadow: -5px -5px 10px rgba(255,255,255,0.05),
             15px 15px 5px rgba(0,0,0,0.05);
}

.box.confirmed h4 {
  color: #FFDC00;
}

.box.recovered h4 {
  color: #2ECC40;
}

.box.deaths h4 {
  color: #800000;
}

.mailto {
  position: fixed;
  color: #363944;
  text-decoration: none;
  transform: rotate(-90deg);
  right: -75px;
  bottom: 100px;
  transition: 3s ease-in-out;
  z-index: 10;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.mailto:hover {
  color: #64e5f9;
}



@media screen and (min-width: 900px) {
  .flex {
    flex-wrap:nowrap;
  }

  .box {
    padding: 2em 1em;
    max-width: 300px;
  }
}

/* Dark Mode */
.darkMode {
  display: flex;
  align-items: center;
  position: absolute;
  right: 30px;
  top: 15px;
}

.checkbox {
  opacity: 0;
  position: absolute;
}

.label {
  background-color: #ff4500;
  display: flex;
  border-radius: 50px;
  height: 26px;
  width: 50px;
  position: relative;
}

.ball {
  width: 22px;
  height: 22px;
  background-color: #fff;
  position: absolute;
  top: 2px;
  left: 2px;
  border-radius: 50%;
  transition: transform 0.2s linear;
}

.checkbox:checked+.label .ball {
  transform: translateX(24px);
  background-color: white;
}

.checkbox:checked+.label {
  background-color: black;
}

body.dark {
  color: var(--White);
  background: white;
}

body.dark .container {
  background: white;
  color: #363944;
}


h5 {
  padding-right: 10px;
}

.head {
  position: absolute;
  z-index: -100;
  height: 600px;
  width: 600px;
  top: 70px;
  right: -5px;
}
